.loading{
  width: 100vw;
  height: 85vh;
  background-color: white;
  display: grid;
  place-items: center;
  max-width: 100%;
}

.loading > div {
  width: 6rem;
  height: 6rem;
  border-bottom: 4px solid #000;
  border-radius: 50%;
  -webkit-animation: load 800ms linear infinite;
  animation: load 800ms linear infinite;
}

@-webkit-keyframes load{
  to{
    -webkit-transform: rotateZ(360deg);
    transform: rotateZ(360deg);
  }
}

@keyframes load{
  to{
    -webkit-transform: rotateZ(360deg);
    transform: rotateZ(360deg);
  }
}

